.notice{
    margin-bottom: 10px;
    padding: 10px;
    box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px, rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
    background-color: white;
    border-radius: 7px;
}
.notice-heading{
    font-size: 18px;
    font-weight: bold;
}
.notice-date-time{
    color: gray;
    font-size: 14px;
}
.notice-paragraph{
    margin: 15px 0px;
}

.notice-e-sign{
    font-size: 17px;
    font-weight: bold;
}
.notice-designation{
    color: gray;
    font-size: 15px;
}