.lecture {
  background-color: white;
  display: flex;
  align-items: center;
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 7px;
  box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px,
    rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
}
.lecture-day-month {
  background-color: #808080;
  text-align: center;
  border-radius: 7px;
  position: relative;
  width: 60px;
  height: 55px;
  margin-right: 15px;
}
.lecture-day-month-container {
  color: white;
  font-weight: bold;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.lecture-day {
  font-size: 20px;
}
.lecture-month {
  font-size: 12px;
  margin-top: -5px;
}
.lecture-title-weekday-time-duration {
}
.lecture-title {
  font-weight: 600;
}
.lecture-weekday-time-duraition {
  font-size: 13px;
  color: grey;
  margin-top: 4px;
  display: flex;
  align-items: center;
}
.lecture-weekday {
}
.lecture-time {
}
.lecture-duration {
}
.lecture-time-dash {
  margin: 0px 8px;
  font-size: 15px;
}
.lecture-info {
}
.lecture-info-attributes {
  display: flex;
  align-items: center;
}
.lecture-info-attribute {
    border: 1px solid;
    padding: 10px 0px;
    text-align: center;
  min-width: 350px;
  max-width: 350px;
  background-color: white;
}
.lecture-info-serial-number {
  text-align: center;
}
.lecture-info-join{
    cursor: pointer;
}
.lecture-info-join:hover{
    background-color: #808080;
    color: white;
}
.lecture-info-serial-number,
.lecture-info-join,
.lecture-info-date,
.lecture-info-time,
.lecture-info-duration,
.lecture-info-attendence {
    min-width: 110px;
    max-width: 110px;
}
