.lectures{
    overflow: auto;
    padding: 10px 10px 10px 0px;
    height: 600px;
}
.lectures-attributes{
    display: flex;
    align-items: center;
    font-weight: bold;
}
.lectures-attribute{
    background-color: #e0efef;
    min-width: 350px;
    max-width: 350px;
    text-align: center;
}
.lectures-serial-number{
}
.lectures-courses{
    border-right: 1px solid;
    border-left: 1px solid;
}
.lectures-serial-number,
.lectures-join,
.lectures-date,
.lectures-time,
.lectures-duration,
.lectures-attendence {
  min-width: 110px;
  max-width: 110px;
  border-right: 1px solid;
  border-left: 1px solid;
}
.lectures-container{
    padding: 0px;
}