.course {
    margin-right: 10px;
    border-radius: 7px;
    background-color: white;
    box-shadow: rgb(0 0 0 / 2%) 0px 1px 3px 0px, rgb(27 31 35 / 15%) 0px 0px 0px 1px;
    min-width: 325px;
    max-width: 325px;
}

.course-container {
    padding: 10px;
}

.course img {
    width: 100%;
    border-radius: 7px 7px 0px 0px;
}

.course-belongs-rating {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
}

.course-rating {
    padding: 3px;
    border-radius: 7px;
}

.course-rating i {
    color: #f8da59;
    margin-right: 5px;
}

.course-rating span {

}

.course-name {
    font-size: 17px;
    font-weight: bold;
    margin-bottom: 10px;
}

.course-progress-bar {
    margin-bottom: 10px;
    width: 100%;
    height: 4px;
    border-radius: 100px;
    background: #e1e5ea;
}

.course-progress {
    border-radius: 100px;
    background-color: #0e0d34;
    height: 100%;
}

.course-progress-daysleft {
    display: flex;
    color: gray;
    justify-content: space-between;
    font-size: 13px;
}

.course-progress-status {

}

.course-progress-status span {

}

