.app {
    display: flex;
    width: 100%;
    height: 100vh;
    overflow: auto;
}

.pages-container {
    width: 100%;
}

/* nav-toogle (open/close) */
.nav-toogle {
    z-index: 9999999;
    background: #0e0d34;
    color: white;
    padding: 5px;
    font-size: 25px;
    position: fixed;
    top: 74px;
    border-radius: 0px 7px 7px 0px;
}

/* info-container */
.info-container {
    border-radius: 7px;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
    padding: 10px;
    background-color: #e0efef;
}

.info-container-heading {
    font-size: 19px;
    font-weight: 500;
}

.info-container-container {
    margin-top: 10px;
    padding: 10px;
    border-radius: 7px;
    background-color: #e1e5ea40;
}
.page{
    padding: 45px 7px;
}
.page-container{
    width: 1100px;
    margin: auto;
}
@media (max-width: 1350px) {
    .page{
        padding: 10px 7px;
    }
    .page-container{
        width: 100%;
    }
}
.welcome{
    font-size: 30px;
    font-weight: 500;
    color: #0e0d34;
}